import React from 'react';

import { RenderingContext, PageItem, DataItem, DataSourceItem } from '@uniformdev/common-client';

export class Component<T = void, TS = any> extends React.Component<
    T & { renderingContext: RenderingContext },
    TS
> {
    /**
     *
     */
    constructor(props: T & { renderingContext: RenderingContext }) {
        super(props);
    }

    get renderingContext(): RenderingContext {
        return this.props.renderingContext;
    }

    get item(): DataSourceItem | PageItem | undefined {
        return this.renderingContext.item;
    }

    get page(): PageItem | undefined {
        return this.renderingContext.page;
    }

    get home(): DataSourceItem | PageItem | undefined {
        return this.renderingContext.home;
    }

    get children(): DataSourceItem[] | DataItem[] | undefined {
        const item: PageItem | undefined = this.item;

        return (item && item.children) || [];
    }
}
